

function CourseAboutSide({ setButtonsParent, CourseInfo, InstructorRef, CourseSyllabusRef, VehicleDetailRef, StudentReviewRef, CourseDetailRef }) {
    let LinkStyle = "no-underline text-black sm:text-white text-4xs sm:text-2xs p-1 py-3 sm:p-4";

    const MoveToOnClick = (Part) => { Part.current?.scrollIntoView() };

    return <div className=' w-full'
        ref={(e) => { setButtonsParent(e) }}
    >
        <span className='flex justify-between px-1 gap-1 sm:gap-10 [&>*]:SemiBold [&>*]:bg-[#ffffff00] [&>*]:cursor-pointer Border'>

        
            <button onClick={() => MoveToOnClick(CourseDetailRef)} className={`${LinkStyle} CourseDetailsNavigateBtns ButtonBorder`}>
                About
            </button>




            <button onClick={() => MoveToOnClick(InstructorRef)} className={`${LinkStyle} CourseDetailsNavigateBtns border-none `}>
                Instructors
            </button>




            <button onClick={() => MoveToOnClick(CourseSyllabusRef)} className={`${LinkStyle} CourseDetailsNavigateBtns border-none `}>
                Syllabus
            </button>



            <button onClick={() => MoveToOnClick(VehicleDetailRef)} className={`${LinkStyle} CourseDetailsNavigateBtns border-none `}>
                Vehicle Details
            </button>




            <button onClick={() => MoveToOnClick(StudentReviewRef)} className={`${LinkStyle} CourseDetailsNavigateBtns border-none `}>
                Reviews
            </button>
        </span>

        <div className=' px-4 sm:p-10 w-full'>

            <h3 className='text-base text-black sm:text-white Boldfont'>Orem Ipsum Dolor Sit Amet, Consec</h3>
            <p className='max-w-[1150px] text-black sm:text-white text-4xs sm:text-[24px] break-words'>
                {CourseInfo?.Course?.Description}
            </p>

        </div>
    </div>
}

export default CourseAboutSide