import React, { useState } from 'react'
import { useEffect } from 'react';
import BuyingSide from './Components/BuyingSide';
import CourseAboutSide from './Components/CourseAboutSide';

const CourseDetails = ({ CourseInfo, InstructorRef, CourseSyllabusRef, VehicleDetailRef, StudentReviewRef, CourseDetailRef }) => {
    const [ButtonsParent, setButtonsParent] = useState();


    let [CourseDetailsNavigateBtns, setCourseDetailsNavigateBtns] = useState([]);
    useEffect(() => {
        if (ButtonsParent) setCourseDetailsNavigateBtns(ButtonsParent.getElementsByClassName("CourseDetailsNavigateBtns"));

    }, [ButtonsParent])

    for (let i = 0; i < CourseDetailsNavigateBtns.length; i++) {

        CourseDetailsNavigateBtns[i]?.addEventListener("click", function (e) {

            for (let j = 0; j < CourseDetailsNavigateBtns.length; j++) {

                if (CourseDetailsNavigateBtns[j].innerHTML === this.innerHTML) {

                    CourseDetailsNavigateBtns[j].classList.add("ButtonBorder")
                    CourseDetailsNavigateBtns[j].classList.remove("border-none")
                } else {
                    CourseDetailsNavigateBtns[j].classList.remove("ButtonBorder");
                    CourseDetailsNavigateBtns[j].classList.add("border-none")
                }
            }

        })

    }

    return (

        <div className='w-full flex justify-center' ref={CourseDetailRef}>
            <div className='flex flex-col items-center w-full'>
                <div className='w-full sm:w-11/12 flex gap-10'>
                    <BuyingSide CourseInfo={CourseInfo} />
                    <CourseAboutSide setButtonsParent={setButtonsParent} CourseInfo={CourseInfo}
                        InstructorRef={InstructorRef} CourseSyllabusRef={CourseSyllabusRef} CourseDetailRef={CourseDetailRef}
                        VehicleDetailRef={VehicleDetailRef} StudentReviewRef={StudentReviewRef} />
                </div>
                <h3 className='text-[38px] text-white PrintBoldfont'>Instructors</h3>
            </div>
        </div>


    )
}


export default CourseDetails